/* Set background image */
.Home {
    /* Set rules to fill background */
    min-height: 100%;
    min-width: 320px;
      
    /* Set up proportionate scaling */
    width: 100%;
    height: auto;
      
    /* Set up positioning */
    position: fixed;
    top: 0;
    left: 0;

    background-image: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.5)), url(/static/media/robonaut.70928045.jpeg);
    
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    z-index: -10;
    
}

/* Set welcome font*/
.Home-welcome {
    padding-top: 200px;
    padding-bottom: 200px;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 50px;
    font-weight: 100;
    text-align: center;
}

.Home-signature{
    position: absolute;
    padding: 10px;
    text-align: center;
    bottom: 0;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 100;
}

.Home-signature:hover{
    color: #d3d3d3;
}

a:hover{
    text-decoration: none;
}
.rbc-calendar {
    min-height: 500px ;
}
.Calendar-welcome{
    text-align: center;
    padding: 10px;
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 40px;
}
.Intro{
    max-height: 500px;
    min-height: 300px;
    background-color: #2980B9;
}
.Intro-welcome{
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 50px;
    text-align: center;
    padding: 10px;
    color: white;
}

.Intro-mission{
    text-align: center;
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 24px;
    padding: 10px;
    color: white;
}

.Intro-whatwedo{
    text-align: center;
    margin: auto;
    max-width: 50em;
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 18px;
    padding: 10px;
    color: white;
}


.Officers-title{
    padding: 10px;
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 50px;
    text-align: center;
}

.Officer{
    position: relative;
    display: block;
    float: left;
    padding: 10px;
    margin: 0 5px;
}

@media screen and (min-width: 992px) {
    .Officers{
        display: flex;
        justify-content: center;
    }
    .Officer{
        position: relative;
        display: block;
        float: left;
        padding: 10px;
        margin: 0 5px;
    }
}

.Name {
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 30px;
}

.Title {
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 24px;
}
.Social-title{
    text-align: center;
    padding: 10px;
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 36px;
}

.Social{
    display: flex;
    justify-content: center;
    text-align: center;
}
.Social-icon{
    position: relative;
    display: block;
    float: left;
    padding: 5px;
    margin: 0 5px;
    width: 250px;
    height: 250px;
}

.Social-handle{
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 18px;
    padding: 5px;
}

.Contact-title{
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 40px;
    padding: 10px;
    text-align: center;
    color: white;
}

.Contact{
    background-color: #2980B9;
}

.Resources{
    display: flex;
    justify-content: center;
    text-align: center;
}

.Resource{
    position: relative;
    display: block;
    float: left;
    padding: 30px;
    margin: 0 5px;
    width: 250px;
    height: 250px;
}

.Resource-hdr{
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 30px;
    color: white;
    padding: 5px;
}

.Resource-ftr{
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 14px;
    color: white;
    padding: 5px;
}

@media screen and (max-width: 640px) {
    .Contact{
        min-height: 670px;
    }
    .Resources{
        display: block;
    }
    .Resource{
        float: none;
        padding: 10px;
        margin: auto;
    }

    .Media{
        min-height: 360px;
    }

    .Social{
        display: block;
    }

    .Social-icon{
        float: none;
        padding: 10px;
        margin: auto;
    }
}

/* Expand icon divs to 300x300 for larger desktop widths */
@media screen and (min-width: 1024px) {
    .Resource{
        position: relative;
        display: block;
        float: left;
        padding: 10px;
        margin: 0 5px;
        width: 300px;
        height: 300px;
    }
}



